@import '../../scss/variables.scss';

.wrapper {
  height: 100vh;
  width: 100vw;
  display: flex;
  position: relative;

  &__side-bar {
    flex: 0 0 64px;

    @media screen and (max-width: $mobile) {
      display: none;
    }
  }

  &__content {
    flex: 1 1 688px;
  }

  &__carousel {
    flex: 1 1.827 688px;

    @media screen and (max-width: $tablet) {
      display: none;
    }
  }
}

.content {
  padding: 32px 64px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media screen and (max-width: $desktop-min) {
    padding: 32px 32px;
  }

  @media screen and (max-width: $mobile) {
    padding: 16px 16px;
  }
}

.main {
  &__title {
    font-weight: 700;
    font-size: 70px;
    line-height: 66px;
    color: $color-black;
    margin: 0;

    &--green {
      color: $color-main-accent;
    }

    @media screen and (max-width: $mobile) {
      font-size: 32px;
      line-height: 100%;
      margin-top: 6px;
    }
  }

  &__subtitle {
    font-weight: 300;
    font-size: 26px;
    line-height: 30px;
    color: $color-gray;
    margin: 32px 0 0;

    @media screen and (max-width: $mobile) {
      margin: 16px 0 0;
      font-size: 18px;
      line-height: 21px;
    }
  }

  &__button {
    margin: 60px 0;
    width: 250px;
    height: 48px;
    border: none;
    border-radius: 8px;
    background: linear-gradient(90deg, $color-main-accent 2.61%, #039f67 112.6%);

    color: $color-white;
    font-size: 18px;
    line-height: 21px;

    @media screen and (max-width: $mobile) {
      margin: 32px -16px;
      width: 100vw;
      border-radius: 0;
    }
  }
}
